const slider = () => {
    const bindSlider = (contentSelector, dotsSelector, dotSelector, prevArrowSelector, nextArrowSelector, activeDotClass) => {
        let sliderIndex = 0;
        const sliderItems = document.querySelectorAll(contentSelector),
            sliderDotsWrap = document.querySelector(dotsSelector),
            sliderDots = sliderDotsWrap.querySelectorAll(dotSelector),
            prevArrow = document.querySelector(prevArrowSelector),
            nextArrow = document.querySelector(nextArrowSelector);
        const interactionWithContent = (i) => {
            sliderItems.forEach(item => {
                item.style.display = 'none';
                item.classList.remove('slideInLeft');
                item.classList.add('animated');
            });
            sliderItems[i].style.display = 'block';
            sliderItems[i].classList.add('slideInLeft');
            sliderDots.forEach(item => {
                item.classList.remove(activeDotClass);
                sliderDots[i].classList.add(activeDotClass);
            });

        };
        const editSlide = (n) => {
            nextArrow.addEventListener('click', () => {
                sliderIndex++;
                if (sliderIndex > sliderItems.length - 1) {
                    sliderIndex = 0;
                }
                interactionWithContent(sliderIndex);
            });
            prevArrow.addEventListener('click', () => {
                sliderIndex--;
                if (sliderIndex < 0) {
                    sliderIndex = sliderItems.length - 1;
                }
                interactionWithContent(sliderIndex);
            });
        };
        const currentSlide = () => {
            sliderDotsWrap.addEventListener('click', (e) => {
                const target = e.target;
                sliderDots.forEach((item, i) => {
                    if (target === item) {
                        interactionWithContent(i);
                    }
                });
            });
        };
        let infinitySlider = () => {
            sliderIndex++;
            if (sliderIndex > sliderItems.length - 1) {
                sliderIndex = 0;
            }
            if (sliderIndex < 0) {
                sliderIndex = sliderItems.length - 1;
            }
            currentSlide(sliderIndex);
            interactionWithContent(sliderIndex);
        };
        interactionWithContent(0);
        currentSlide();
        editSlide();
        setInterval(infinitySlider, 6000);
        sliderItems.forEach(item => {
            let mc = new Hammer(item);
            mc.on("swipeleft", function (ev) {
                sliderIndex++;
                if (sliderIndex > sliderItems.length - 1) {
                    sliderIndex = 0;
                }
                interactionWithContent(sliderIndex);
            });
            mc.on("swiperight", function (ev) {
                sliderIndex--;
                if (sliderIndex < 0) {
                    sliderIndex = sliderItems.length - 1;
                }
                interactionWithContent(sliderIndex);
            });
        });
    };
    bindSlider('.promo-carousel__item', '.promo-dots', '.promo__dot ', '.prev-arrow_promo ', '.next-arrow_promo', 'promo__dot_active');
};
export default slider;